<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <ellipse :cx="ellipseConfig.cx"
                    :cy="ellipseConfig.cy"
                    :rx="ellipseConfig.rx"
                     :ry="ellipseConfig.ry"
                     :stroke="ellipseConfig.stroke"
                    :stroke-width="ellipseConfig.strokeWidth"
                    :fill="ellipseConfig.fill" ></ellipse>
          </svg>
        </div>
        <div class="svg-config-form__wrapper">
          <el-form ref="ellipseConfigForm" :model="ellipseConfig" label-width="200px">
            <el-form-item label="圆心坐标(x)">
              <el-slider
                  v-model="ellipseConfig.cx"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="圆心坐标(y)">
              <el-slider
                  v-model="ellipseConfig.cy"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="水平半径(rx)">
              <el-slider
                  v-model="ellipseConfig.rx"
                  :min="20"
                  :max="100"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="垂直半径(ry)">
              <el-slider
                  v-model="ellipseConfig.ry"
                  :min="20"
                  :max="100"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="笔划颜色(stroke)">
              <el-color-picker v-model="ellipseConfig.stroke" show-alpha >
              </el-color-picker>
              {{ellipseConfig.stroke}}
            </el-form-item>

            <el-form-item label="笔划宽度(strokeWidth)">
              <el-slider
                  v-model="ellipseConfig.strokeWidth"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="填充颜色(fill)">
              <el-color-picker v-model="ellipseConfig.fill" show-alpha >
              </el-color-picker>
              {{ellipseConfig.fill}}
            </el-form-item>

          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
export default {
  name: 'SVGDemo001',
  data () {
    return {
      ellipseConfig: {
        cx: 100,
        cy: 50,
        rx: 40,
        ry: 40,
        stroke: 'rgba(0, 0, 0, 0.8)',
        strokeWidth: 2,
        fill: 'rgba(255, 69, 0, 0.68)'
      }
    }
  }
}
</script>

<style scoped>

</style>